import { Content, Title } from "~/components/settings/layout";
import Account from "~/components/settings/account";
import Bio from "~/components/profile/profile";
import type { AuthUser } from "~/services/session.server";
import { getUser } from "~/services/session.server";
import type { LoaderArgs } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import gems from "../../public/assets/images/logo/gem.png";
import useProfileContent from "~/services/nostr/profiles/content";

export async function loader({ request }: LoaderArgs): Promise<any> {
  const user = await getUser(request);
  return user;
}

export default function Profile() {
  const user: AuthUser = useLoaderData();
  const profileData = useProfileContent(user?.pubkey);
  return (
    <>
      <Content>
      <div >
      {/* Banner Section */}
      <div
        className="bg-cover bg-center h-64"
        style={{ backgroundImage: 'url("/path-to-your-banner-image.jpg")' }}
      >
        {/* Overlay for banner */}
        <div className="bg-black bg-opacity-40 h-64 flex items-center justify-center">
          <h1 className="text-3xl font-semibold text-white">
            Welcome to My Profile
          </h1>
          <p className="text-lg text-white">
            Discover more about me and my interests.
          </p>
        </div>
      </div>

      {/* Profile Information Section */}
      <div className="max-w-3xl mx-auto mt-4">
        <div className="bg-white shadow-md rounded-lg p-6">
          {/* Profile Picture */}
          <img
            src={gems}
            alt="Profile Picture"
            className="rounded-full w-32 h-32 mx-auto -mt-16 border-4 border-white shadow-lg"
          />

          <h2 className="text-xl font-semibold mt-4">{profileData.name}</h2>
          <p className="text-gray-500">Your Bio or Description</p>
          <div className="mt-4">
            {/* Other profile information */}
          </div>
        </div>
      </div>
      </div>
        {/* <Title title={"PROFILE"} />
        <Bio user={user} />
        <div className="px-10 flex justify-center">
          <div className="w-[960px] h-[2px] bg-c-gray mt-7" />
        </div>
        <div className="px-10 flex justify-center">
          <div className="w-[960px] h-[2px] bg-c-gray mt-5"></div>
        </div>
        <Account /> */}
      </Content>
    </>
  );
}
